import { Component, OnInit, ViewChild } from '@angular/core';
import { MatTableDataSource } from '@angular/material/table';
import { MatPaginator } from '@angular/material/paginator';
import { Router, ActivatedRoute } from "@angular/router";
import { Configuration } from 'src/app/app.configuration';
import { DataService } from 'src/app/shared/services/data-service';
import { StorageConfiguration } from 'src/app/shared/storage-setting/storage-configuration';
import { ToastrService } from 'ngx-toastr';
import { MatDialog } from '@angular/material/dialog';
import { MatSort } from '@angular/material/sort';
import { NgxSpinnerService } from 'ngx-spinner';
import { ConfirmDialogComponent } from 'src/app/shared/confirm-dialog/confirm-dialog.component';

@Component({
  selector: 'app-category',
  templateUrl: './category-list.component.html',
  styleUrls: ['./category-list.component.scss']
})

export class CategoryListComponent implements OnInit {

  displayedColumns: string[] = ['position', 'categoryName', 'color', 'codingAbbreviation', 'createdBy', 'createdDate'];
  totalRecords: any;
  categoryList = [];
  @ViewChild(MatSort, { static: true }) sorting: MatSort;
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  pageNumber = 1;
  categorySearchValue: any;
  defaultImg = 'assets/images/profile.jpg';
  confirmDialogRef: any;
  isEdit: boolean = false;
  categoryId: any;
  isGlobalSynonymEdit = false;
  role: any;

  constructor(
    private router: Router,
    private configuration: Configuration,
    private dataService: DataService,
    private storageConfiguration: StorageConfiguration,
    private toastr: ToastrService,
    private _matDialog: MatDialog,
    private spinner: NgxSpinnerService
  ) { }


  ngOnInit() {
    this.isGlobalSynonymEdit = JSON.parse(this.storageConfiguration.sessionGetItem(this.storageConfiguration.isGlobalSynonymEdit));
    this.role = this.storageConfiguration.sessionGetItem(this.storageConfiguration.name);
    if (this.role != 'staff') {
      this.displayedColumns.push('actions')
    }
    this.getCategoryList();
  }

  getCategoryList() {
    this.spinner.show();
    this.dataService.getAll(this.configuration.category + '?sortBy=sortOrder&orderBy=asc').subscribe((data: any) => {
      let categoryList = data.data;
      this.totalRecords = data.totalRecords;
      this.categoryList = this.categoryListTableData(categoryList);
      this.spinner.hide();
    }, err => {
      this.spinner.hide();
    });
  }

  nextPage(event) {
    this.spinner.show();
    this.pageNumber = event.pageIndex + 1;
    this.dataService.getAll(this.configuration.category + '?pageNumber=' + this.pageNumber + '&sortBy=createdDate&orderBy=desc').subscribe((data: any) => {
      let categoryList = data.data;
      this.totalRecords = data.totalRecords;
      this.categoryList = this.categoryListTableData(categoryList);
      this.spinner.hide();
    }, err => {
      this.spinner.hide();
    });
  }

  sortTable() {
    this.spinner.show();
    this.dataService.getAll(this.configuration.category + '?sortBy=' + this.sorting.active + '&orderBy=' + this.sorting.direction).subscribe((data: any) => {
      let categoryList = data.data;
      this.categoryList = this.categoryListTableData(categoryList);
      this.spinner.hide();
    }, err => {
      this.spinner.hide();
    });
  }

  async searchcategory() {
    if (this.categorySearchValue != undefined && this.categorySearchValue != "") {
      await this.dataService.getAll(this.configuration.category + '?type=' + this.categorySearchValue + '&sortBy=createdDate&orderBy=desc').subscribe((data: any) => {
        let categoryList = data.data;
        this.totalRecords = data.data.length;
        this.pageNumber = 1;
        this.paginator.pageIndex = 0;
        this.categoryList = this.categoryListTableData(categoryList);
      }, err => {
        this.spinner.hide();
      });
    } else {
      this.getCategoryList()
    }
  }

  categoryListTableData(categoryList) {
    let categoryListData = [];
    categoryList.forEach((element, index) => {
      categoryListData.push({
        id: element._id,
        position: (index + 1) + (this.pageNumber - 1) * 10,
        categoryName: element.type,
        color: element.color,
        createdBy: element.createdBy,
        codingAbbreviation: element.abbreviation ? element.abbreviation : '',
        createdDate: element.createdDate,
        isActive: element.isActive,
        isDeleted: element.isDeleted
      });
    });

    return categoryListData;
  }

  categoryStatusToggle(event, categoryId) {
   
    let categoryStatus = event.checked == true ? 'Activate' : 'Deactivate';
    let message = {
      message: 'Do you want to ' + categoryStatus + ' this Six Essentials?'
    }
    this.confirmDialogRef = this._matDialog.open(ConfirmDialogComponent, {
      panelClass: 'confirm',
      data: message
    });

    this.confirmDialogRef.afterClosed().subscribe(result => {
      let editElement = this.categoryList.findIndex(element => element.id == categoryId);
      if (result == true) {
        this.categoryList[editElement].isActive = event.checked;
        this.categoryActivateDeactivate(event, categoryId);
      } else {
        this.categoryList = JSON.parse(JSON.stringify(this.categoryList));
      }
    })
  }

  categoryActivateDeactivate(event, categoryId) {
    if (event.checked === false) {
      let updateModel = { isActive: false }
      this.dataService.updateSwitch(categoryId, this.configuration.category + '/', updateModel).subscribe(
        (data: any) => {
         
          this.toastr.error('De-activated successfully.')
        }
      )
    }
    else {
      let updateModel = { isActive: true }
      this.dataService.updateSwitch(categoryId, this.configuration.category + '/', updateModel).subscribe(
        (data: any) => {
         
          this.toastr.success('Activated successfully.')
        }
      )
    }
  }

  categoryDelete(categoryId) {
    let message = {
      message: 'Do you want to delete this Six Essentials?'
    }
    this.confirmDialogRef = this._matDialog.open(ConfirmDialogComponent, {
      panelClass: 'confirm',
      disableClose: false,
      data: message
    });
    this.confirmDialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.dataService.delete(categoryId, this.configuration.category + '/').subscribe(
          (data: any) => {
            this.toastr.error('Deleted successfully.');
            this.getCategoryList();
          }
        )
      }
    });
  }

  edituserdetail(id) {
    this.router.navigate(['/map/category/category-details'], { queryParams: { categoryId: id, isEdit: true } });
  }

  addcategory() {
    this.router.navigate(['/map/category/category-details'], { queryParams: { isEdit: false } });
  }
}
